<template>
    <div>
        <div id="errorTu"></div>
        <p class="error_point"></p>
        <div id="WangEditor"></div>
        <div style="width:70%;margin:0 auto">
            <el-button type="success" style="margin-top:10px;float:left"></el-button>
            <el-button type="" style="margin-top:10px;float:left"></el-button>
        </div>
    </div>
</template>
<script>
import WangEditor from "wangeditor";
export default {
  data() {
    return {
      editor: null
    };
  },

  mounted() {
    this.editor = new WangEditor("#WangEditor"); //divid #
    //  onchange 
    this.editor.change = function() {
      console.log(this.txt.html());
    };
    this.editor.create(); // 
    this.editor.txt.html("<p>...</p>"); //txt  $txt

    var myChart1 = echarts.init(document.getElementById("errorTu"));
    var option1 = {
      title: {
        left: "center"
      },
      tooltip: {},
      xAxis: {
        data: ["1", "2", "3", "4", "5", "6", "7"]
      },
      yAxis: {},
      series: [
        {
          name: "",
          type: "line",
          data: [5, 20, 36, 10, 10, 20, 10]
        }
      ]
    };
    myChart1.setOption(option1);
  }
};
</script>

<style scoped>
#errorTu {
  width: 95%;
  margin: 0 auto;
  height: 400px;
}
#WangEditor {
  width: 70%;
  text-align: left;
  margin: 0 auto;
}
.w-e-text-container {
  height: 150px !important;
}
.error_point{
  text-align: left;
  width:80%;
  margin: 0 auto;
  font-size: 1.4em;
  margin-bottom: 20px;
  color: red;
  

}
</style>



